    <Card title="Weaving the Social Fabric with Compassionate Warp and Dedicated Weft" bordered={false}>
      <p>
TRINJAN is the symbol of caring and sharing in Punjabi community. Under this umbrella, women used to spin, weave, knit, sing, celebrate, appreciate and create different kinds of artistic products. It used to be a platform of collective teaching and learning with a close association of nature and culture. The type of development model which is being adopted in last 5-6 decades didn’t allow women to enhance the collective working and its spread to the next generation. Younger generation is getting school and college education where these traditional creative skills are not considered worthy. That’s why the spinning wheel which is a symbol of life is being burnt by people in chullah as fuel wood. Same has happened with the handlooms and other material used for handicrafts and textile items.</p>
<p>With this background and zeal to prevent this great traditional art from being vanished, KVM started working for its revival. For us, these are not mere products that come out, there is a connect of human and nature that can be clearly seen along with the association and owness. In order to prevent this heritage, Organic Desi Cotton is being grown by the farmers, procured by KVM, hand spun, naturally dyed and hand woven by the rural artisans. Along with this there are artisans who do hand embroidery, make different grass crafts and other decorative products. All these skills have been promoted and developed under TRINJAN.</p>
<p>
It has freedom for the artisans as they can work from home, most of the artisans are women. It’s a very good opportunity for the livelihood generation and pass on this skill to the next generation. KVM believes that revival of traditional, slow, natural and cultural wisdom shall help addressing the physical, mental and emotional health crisis that people have been going through.</p>
<p>
This is a people’s initiative where KVM is acting as a platform only. We urge you to come forward and support the work in whatever capacity you could i.e. financially, providing marketing, documentation of folklore associated, reaching out to more people, helping in providing the required infrastructure and by visiting any part of this value chain any time. It’s a call to all those concerned and worried about the contemporary model of development to contribute for the change we all wish to have for a better world.
      </p>
    </Card>

